import React, {useState} from 'react';
import { Text,View } from 'react-native';
import { Image, Row, Button,Spinner, Nav,Navbar,Container,NavDropdown,Form,FormControl,Offcanvas } from 'react-bootstrap';
import ContactView from './Views/ContactView';
import FormationView from './Views/FormationView';
import AproposView from './Views/AproposView';
import Config from './Views/ConfigSite';
import HomeView from './Views/HomeView';
import { getIcon } from './Model/Functions';

  class App extends React.Component{

    constructor(props) {
      super(props)
      this.state = {
        isLoading : false,
        page : this.getHome()

      }
    }
    getLoading (){
    return (
      <View style={{justifyContent : 'space-between', alignItems : 'center'}}>
          <Spinner animation="border" variant="primary" />
      </View>
      
    )
  }
    getHome = ()=> {
      return <HomeView />
    }
    getTemplate () {
      return (
        <>
         <Navbar bg="dark" variant="primary" expand={false}>
  <Container fluid>
    <Navbar.Brand href="#">
            <Image onClick={() => this.setState({page : <HomeView />})} src={getIcon()} style={{width: 50, height : 50}}  />
    </Navbar.Brand>
    <Navbar.Brand onClick={() => this.setState({page : <FormationView />})} href="#home1">Formations</Navbar.Brand>
    <Navbar.Brand onClick={() =>  this.setState({page : <ContactView />})} href="#home2">Contacts</Navbar.Brand>

    <Navbar.Toggle style={{borderColor : '#fff'}} aria-controls="offcanvasNavbar" />
    <Navbar.Offcanvas
      id="offcanvasNavbar"
      aria-labelledby="offcanvasNavbarLabel"
      placement="end"
    >
      <Offcanvas.Header closeButton>
        <Offcanvas.Title id="offcanvasNavbarLabel">
         <Image src={getIcon()} style={{width: '100%', height : 50}} />
</Offcanvas.Title>
      </Offcanvas.Header>
      <Offcanvas.Body>
        <Nav className="justify-content-end flex-grow-1 pe-3">
          <Nav.Link onClick={() =>  this.setState({page : <HomeView />})} href="#action1">Home</Nav.Link>
          <Nav.Link onClick={() =>  this.setState({page : <FormationView />})} href="#action1">Formation</Nav.Link>
          <Nav.Link onClick={() =>  this.setState({page : <ContactView />})} href="#action2">Nos Contacts</Nav.Link>
          <Nav.Link onClick={() =>  this.setState({page : <AproposView />})} href="#action2">À propos</Nav.Link>
        </Nav>
      </Offcanvas.Body>
    </Navbar.Offcanvas>
  </Container>
</Navbar>
<Container style={{flex: 1}} >
  
{(this.state.isLoading) ? this.getLoading()  : this.state.page}
</Container>
        </>

      )
    }
      render () {
        return this.getTemplate();
      }
  }
export default App;