import React, { Component, useState } from 'react';
import * as WebBrowser from 'expo-web-browser';
import {Button, SocialIcon} from 'react-native-elements';


export function getIcon () {
    return require('../assets/image/icon.jpg');
}
export const OpenLink = ({title, link, isSocial = false, typeSocial}) => {

    const [result, setResult] = useState(null);

    const _handlePressButtonAsync = async link => {
        let result = await WebBrowser.openBrowserAsync(link);
        setResult(result);
    };
        console.log(link);
    if (isSocial) {
return (
                      <SocialIcon type={typeSocial} onPress={_handlePressButtonAsync} />
    )
    }else {
return (
              <Button title={title} onPress={_handlePressButtonAsync} />
    )
    }
    
}

